.tippy-tooltip.l-jove-theme {
  background-color: #434343;
  padding: 0;
  box-shadow: 4px 4px 4px 0px rgba(191, 191, 191, 0.75);
}
.tippy-tooltip.l-jove-theme .l-buttonbar-content-container {
  width: auto;
  height: 54px;
  display: flex;
}
.tippy-tooltip.l-jove-theme .l-buttonbar-content-container .l-buttonbar-icon-container {
  height: 40px;
  left: 0px;
  top: 8px;
  width: 40px;
  margin-right: 10px;
  margin-left: 10px;
  position: relative;
  cursor: pointer;
}
.tippy-tooltip.l-jove-theme .l-buttonbar-content-container .l-buttonbar-icon-container:hover {
  background-color: #1f1f1f;
  border-radius: 5px;
}
.tippy-tooltip.l-jove-theme .l-buttonbar-content-container .l-buttonbar-icon-container .l-buttonbar-icon {
  height: 20px;
  width: 20px;
  left: 10px;
  top: -3px;
  font-size: 28px;
  position: absolute;
  flex-direction: column;
}
.tippy-tooltip.l-jove-theme .l-buttonbar-content-container .l-buttonbar-separator {
  width: 1px;
  height: 38px;
  background: #535353;
  position: relative;
  left: 0px;
  top: 8px;
}
.tippy-tooltip.l-jove-theme[data-animatefill] {
  background-color: transparent;
}
.tippy-tooltip.l-jove-theme .tippy-roundarrow {
  fill: #434343;
}
.defaultButtonContainer {
  height: 52px;
  box-sizing: border-box;
  border-top: 1px solid #D1D1D1;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  display: flex;
  align-items: center;
  margin: 0 10px;
  overflow: hidden;
}
.defaultButtonContainer button {
  outline: none;
  color: #01579b;
  background-color: transparent;
  display: inline-block;
  padding: 6px 0px;
  margin: 0 17px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  border-radius: 0;
}
.defaultButtonContainer button .btn-text:hover {
  text-decoration: underline;
}
.defaultButtonContainer button .la {
  padding-right: 7px;
  font-size: 14px;
}
.defaultButtonContainer button:disabled {
  cursor: not-allowed;
  box-shadow: none;
  opacity: .65;
  text-decoration: none;
}
.defaultButtonContainer button:disabled .btn-text:hover {
  text-decoration: none;
}
.defaultButtonContainer .rightSeparator {
  display: inline-block;
  border-right: 1px solid #D1D1D1;
  height: 34px;
}
.defaultButtonContainer button.defaultBtn-hintEnabled-icon {
  color: white;
  border-radius: 4px;
  padding-left: 7px;
  background-color: #648FC4;
}
.defaultButtonContainer button.defaultBtn-hintEnabled-icon .btn-text {
  border-right: 1px solid;
  padding-right: 7px;
}
.defaultButtonContainer button.defaultBtn-hintEnabled-icon .la-close {
  display: inline-block;
  padding-left: 7px;
}
.defaultButtonContainer .defaultBtn-check-mywork-icon,
.defaultButtonContainer .defaultBtn-tryAgain {
  width: 117px;
}
.defaultButtonContainer .defaultBtn-hintEnabled-icon {
  margin-left: 10px;
}
.l-fullscreen-btn-bar-container {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar {
  height: auto;
  text-align: center;
  box-shadow: 4px 4px 4px 0px rgba(191, 191, 191, 0.75);
  -webkit-filter: drop-shadow(4px 4px 4px #acacac);
          filter: drop-shadow(4px 4px 4px #acacac);
  border-radius: 5px;
  background-color: #434343;
  position: relative;
  left: 20px;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar .l-fs-btn-bar-icon-container {
  height: 40px;
  display: inline-flex;
  align-items: center;
  padding-right: 10px;
  padding-left: 10px;
  cursor: pointer;
  color: white;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar .l-fs-btn-bar-icon-container:hover {
  background-color: #1f1f1f;
  border-radius: 5px;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar .l-fs-btn-bar-icon-container .l-buttonbar-icon {
  font-size: 20px;
  padding-bottom: 3px;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar .l-fs-btn-bar-icon-container .l-buttonbar-icon-text {
  font-size: 15px;
  padding-left: 10px;
  padding-top: 2px;
  font-family: 'Open Sans', sans-serif;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar .l-fs-btn-bar-icon-container.text-only .l-buttonbar-icon-text {
  padding: 4px;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar .l-fs-btn-bar-separator {
  width: 1px;
  height: 25px;
  background: #535353;
  display: inline-block;
  top: 8px;
  position: absolute;
}
.l-fullscreen-btn-bar-container .l-fullscreen-btn-bar.right-align {
  left: -20px;
}
