.l-matcher-modal {
  display: flex;
  height: 100%;
  overflow: hidden;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1000;
}
.l-matcher-modal .curtain {
  background-color: #000;
  height: 100%;
  opacity: 0.5;
  position: absolute;
  width: 100%;
}
.l-matcher-modal .main-container {
  background-color: #fff;
  margin: auto;
  opacity: 1;
  height: 95%;
  width: 95%;
  z-index: 1;
}
.l-matcher-modal .main-container .matcher-header {
  background-color: #f4f4f4;
  height: 40px;
  font-family: "Trebuchet MS";
}
.l-matcher-modal .main-container .matcher-header .heading {
  display: inline-flex;
  height: 100%;
}
.l-matcher-modal .main-container .matcher-header .heading .heading-icon {
  display: inline-flex;
  height: 20px;
  margin: auto 10px;
}
.l-matcher-modal .main-container .matcher-header .heading .heading-text {
  color: #343434;
  display: inline-flex;
  font-size: 18px;
  font-weight: bold;
  margin: auto;
}
.l-matcher-modal .main-container .matcher-header .buttons {
  float: right;
  padding-right: 10px;
  display: inline-flex;
  height: 100%;
}
.l-matcher-modal .main-container .matcher-header .buttons .crossBtn {
  cursor: default;
  display: inline-flex;
  margin: auto 5px;
  font-weight: bold;
  width: 20px;
}
.l-matcher-modal .main-container .matcher-header .buttons .crossBtn:hover {
  color: #000;
}
.l-matcher-modal .main-container .matcher-container {
  box-sizing: border-box;
  padding: 30px;
  height: calc(100% - 40px);
  display: flex;
  justify-content: space-around;
  overflow: scroll;
}
.l-matcher-modal .main-container .matcher-container .left-container {
  display: inline-block;
  position: relative;
  top: 0px;
  width: 100%;
  height: calc(90% - 50px);
}
.l-matcher-modal .main-container .matcher-container .editor-header {
  display: flex;
  padding: 4px 0px;
  justify-content: space-between;
}
.l-matcher-modal .main-container .matcher-container .editor-header .editor-name {
  font-size: 20px;
}
